.preconter_list{
    width: 60%;
    height: 60vh;
    display: flex;
    margin-top: 6vh;
    justify-self: center;
    flex-direction: column;
    overflow: scroll;

    .content_info{
        width: 90%;
        display: flex;
        flex-direction: row;
        justify-content: space-around;
        align-items: center;
        border: 1px solid #707070;
        border-radius: 0.5vw;
        padding: 0.5vw 0.9vw;
        margin-bottom: 1vw;
        color: #303030;
        cursor: pointer;
        outline: none;

        .text_info{
            width: 95%;
            display: flex;
            flex-direction: column;

            .info_date_numberPqx{
                display: flex;
                align-items: center;
                flex-direction: row;
                font-family: "Poppins";
                margin-bottom: 0.5vw;
                p{
                    font-weight: 300;
                    font-size: 1vw;
                    margin-right: 0.8vw;
                }

                .letter-green{
                    color:#1ABC9C;
                }
            }
        }
        .storeHouseStock_elements_img_arrow{
            width: .7vw;
            height: .7vw;
        }
    }
}