.preconter_list_statusCX{
    width: 65%;
    height: 65vh;
    display: flex;
    margin-top: 16vh;
    align-items: center;
    flex-direction: column;
    overflow: scroll;
    overflow-x: hidden;
    justify-self: center;

    .content_info_statusCX{
        width: 90%;
        display: flex;
        flex-direction: row;
        justify-content: space-around;
        align-items: center;
        border: 1px solid #707070;
        border-radius: 0.5vw;
        padding: 0.5vw 0.9vw;
        margin-bottom: 1vw;
        color: #303030;
        cursor: pointer;
        outline: none;

        .text_info_statusCX{
            width: 95%;
            display: flex;
            flex-direction: column;

            .info_date_number_statusCX{
                display: flex;
                align-items: center;
                flex-direction: row;
                font-family: "Poppins";
                margin-bottom: 0.2vw;
                font-weight: 300;
                font-size: 1.2vw;
                color: #303030;
                
            }
        }
        .storeHouseStock_elements_img_arrow{
            width: .7vw;
            height: .7vw;
        }
    }
}