.preconter_list_cx{
    width: 70%;
    height: 50vh;
    display: flex;
    margin-top: 2vw;
    align-items: center;
    flex-direction: column;
    overflow: scroll;
    overflow-x: hidden;
    justify-self: center;

    .content_info_cx{
        width: 95%;
        display: flex;
        flex-direction: row;
        justify-content: space-around;
        align-items: center;
        border: 1px solid #707070;
        border-radius: 0.5vw;
        padding: 0.5vw 0.9vw;
        margin-bottom: 1vw;
        color: #303030;
        cursor: pointer;
        outline: none;


        .text_info_cx{
            width: 95%;
            display: flex;
            flex-direction: column;
            font-family: "Poppins";
            font-size: 1vw;
            margin-bottom: 0.5vw;

            .info_date_numberPqx_cx{
                display: flex;
                align-items: center;
                flex-direction: row;
                font-family: "Poppins";
                color: #303030;
                font-weight: 300;
                font-size: 1vw;
                // margin-bottom: 0.5vw;

                .txt_status{
                    color:#1ABC9C !important;
                }

                p{
                    /* font-weight: 300;
                    font-size: 1vw; */
                    color: #303030;
                    font-weight: 300;
                    margin-right: 0.8vw;
                }
            }
        }
        .storeHouseStock_elements_img_arrow{
            width: .7vw;
            height: .7vw;
        }
    }
}