.c-container3{
    display: grid;
    grid-template-rows: 1fr .4fr .1fr;
    width: 37vw;
    height: 62vh;
   
    .container-img{
        display: flex;
        width: 100%;
        height: 40vh;
        justify-content: center;

        img{
            display: flex;
            background: blue;
        }
    }
    .container-carrousel{

        display: flex;
        width: 100%;
        height: 18vh;
        flex-direction: row;
        justify-content: flex-start;
        align-content: center;
        align-items: center;
        overflow: auto;

        .activo{
            border: black solid 1px;
            
        }
        .container-img{
            display: grid;
            height: 15vh;
            width: 7vw;
            margin-right: 1vw;
        img{
            background: blue;
            align-self: center;
            justify-self: center;
            height: 14vh;
            width: 14vh;
        }
        }
    
}
   .container-btn{
    display: flex;
    flex-direction: row;
    width: 100%;
    height: 7vh;
    justify-content: flex-end;
    align-items: center;

    .btn-do{
        width: 3vw;
        height: 6vh;
    }

    .btn-de{
        width: 9vw;
        height: 6vh;
    }

   }
}