.container-encuestas-detalles {
    display: flex;
    width: 100%;
    justify-content: center;
    align-items: start;
    padding-top: 16vh;

    .container-encuestas-colums-detalles {
        width: 90%;
        display: grid;
        grid-template-columns: 0.8fr 1fr;
        justify-content: start;
        align-items: start;
        grid-gap: 7vw;
        margin-top: 5vh;

        .container-sec-left {
        }

        .container-sec-rigth {
            display: flex;
            flex-direction: column;
            width: 95%;

            .container-medico {
                height: 68vh;
                overflow: overlay;
                padding-left: 4px;
            

                .text-medico-header {
                    margin-top: 3vh;
                    color: #094070;
                    font-family: "Poppins";
                    font-weight: 300;
                    text-align: center;
                    margin-bottom: 3vh;
                }

                
            }

            .btn-detalles-encuesta {
                background: linear-gradient(90deg, #37a7b9 10%, #094070 100%);
                color: #fff;
                padding: 1vh 2vw;
                font-family: "Poppins";
                font-size: 0.9vw;
                border-radius: 15px;
                width: 100px;
                margin-left: 32vw;
                margin-top: 2vh;
                padding: 5px;
            }
        }
    }
}
