.storeHouseStock_elements_container_info{
    height: 60vh;
    display: flex;
    margin-top: 2vw;
    align-items: center;
    flex-direction: column;
    width: 95%;
    overflow: scroll;
    .storeHouseStock_elements_info_container{
        width: 95%;
        display: flex;
        flex-direction: row;
        justify-content: space-around;
        align-items: center;
        border: 1px solid #707070;
        border-radius: .5vw;
        padding: .5vw .9vw;
        margin-bottom: 1vw;
        color: #303030;
        cursor: pointer;
        outline: none;
        .storeHouseStock_elements_info_elements_container{
            width: 95%;
            display: flex;
            flex-direction: column;
            .storeHouseStock_elements_info_date_container{
                display: flex;
                align-items: center;
                flex-direction: row;
                font-family: 'Poppins';
                margin-bottom: .5vw;
                .storeHouseStock_elements_info_date_title{
                    font-weight: 400;
                    font-size: 1vw;
                    margin: 0 1vw 0 0;
                }
                .storeHouseStock_elements_info_date{
                    font-weight: 300;
                    font-size: 1vw;
                }
            }
            .storeHouseStock_elements_info_ubicacion_container{
                display: flex;
                font-size: 1vw;
                align-items: center;
                flex-direction: row;
                .storeHouseStock_elements_info_ubicacion_element{
                    margin-right: 1vw;
                    font-weight: 300;
                }
            }
        }
        .storeHouseStock_elements_img_arrow{
            width: .7vw;
            height: .7vw;
        }
    }

}