.c-FactoryConfigModalNewConfig
    width: 100%
    display: flex
    flex-direction: column
    justify-content: center
    align-items: center
    input[type=number]::-webkit-inner-spin-button, 
    input[type=number]::-webkit-outer-spin-button
      -webkit-appearance: none
      margin: 0
    .c-FactoryConfigModalNewConfig-c-item
        width: 90%
        display: flex
        flex-direction: row
        justify-content: space-between
        align-items: center
        margin-bottom: 10px
        padding-bottom: 10px
        border-bottom: 1px solid #a8a8a8
        .c-FactoryConfigModalNewConfig-item-name
            font-family: 'Poppins'
            font-weight: 300
            font-size: 18px
            color: #696969
            margin-left: 20px
            border: none
            width: 90%
            outline: none
    .c-FactoryConfigModalNewConfig-button
        width: 60%
        margin-top: 20px
        padding: 10px
        border-radius: 20px
        border: none
        background: linear-gradient(270deg, rgba(26,188,156,1) 0%, rgba(55,167,185,1) 91%)
        color: #fff
        outline: none
        cursor: pointer
        font-size: 16px