.container-ModalOrderTeamATQ-all{
    width: 100%;
    display: grid;
    grid-template-columns: 1fr;
    justify-content: center;
    align-items: center;
    font-family: "Poppins";
    .container-ModalOrderTeamATQ-list-items{
        width: 100%;
        margin-bottom: 20px;
        font-family: "Poppins";
        .container-ModalOrderTeamATQ-items-scroll{
            height: 10vw;
            overflow-y: scroll;
            .container-ModalOrderTeamATQ{
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: center;
                width: 100%;
            .ModalOrderTeamATQ-c-item{
                width: 100%;
                display: grid;
                grid-gap: 1vw;
                grid-template-columns: .1fr 1fr .1fr .1fr;
                align-items: center;
                padding-bottom: 5px;
                .ModalOrderTeamATQ-c-item-numbers-position{
                    width: 1.7vw;
                    height: 1.7vw;
                    border-radius: 50%;
                    color: #fff;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    .ModalOrderTeamATQ-c-item-numbers-position-number{
                        margin: 0;
                        padding: 0;
                    }
                }
                .ModalOrderTeamATQ-c-item-numbers-position-first{
                    background: #1ABC9C;
                }
                .ModalOrderTeamATQ-c-item-numbers-position-second{
                    background: #37A7B9;
                }
                .ModalOrderTeamATQ-c-item-numbers-position-tird{
                    background:#094070;
                }
                .ModalOrderTeamATQ-item-name{
                    font-family: 'Poppins';
                    font-weight: 300;
                    font-size: 18px;
                    color: #696969;
                    text-align: left;
                }
                .ModalOrderTeamATQ-item-arrow{
                    display: inline-block;
                    width: 1.3vw;
                    height: 1.3vw;
                    margin-right: .5rem;
                    background-size: cover;
                    cursor: pointer;
                }
            }
        }
    }
        .ModalOrderTeamATQ-button{
            cursor: pointer;
            outline: none;
            margin-top: 1vw;
            width: 50%;
            padding: .5vw;
            border-radius: 1vw;
            background: linear-gradient(23deg, rgba(55,167,185,1) 0%, rgba(9,64,112,1) 100%);
            color: #fff;
            box-shadow: 6px 5px 4px 0px rgb(0 0 0 / 25%);
            border: none;
        }
    }
}