.container-ModalATQSelected-all{
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;

    .container-ModalATQSelected-list-items{
        width: 90%;
        margin-bottom: 20px;
        font-family: "Poppins";
        .container-ModalATQSelected-items-scroll{
            height: 10vw;
            overflow-y: scroll;
            .container-ModalATQSelected{
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: center;
                width: 100%;
            .ModalATQSelected-c-item{
                width: 100%;
                display: grid;
                grid-template-columns: 1fr .1fr;
                align-items: center;
                padding-bottom: 5px;
                border-bottom: 1px solid #a8a8a8;
                .ModalATQSelected-c-item-status{
                    width: 1vw;
                    height: 1vw;
                    border-radius: 50%;
                }
                .ModalATQSelected-c-item-status-active{
                    background: green;
                }
                .ModalATQSelected-c-item-status-inactive{
                    background: red;
                }
                .ModalATQSelected-item-name{
                    font-family: 'Poppins';
                    font-weight: 300;
                    font-size: 18px;
                    color: #696969;
                    text-align: left;
                }
                .ModalATQSelected-item-check{
                    display: inline-block;
                    width: 18px;
                    height: 18px;
                    margin-right: .5rem;
                    background-size: cover;
                }
            }
        }
    }
        .ModalATQSelected-button{
            cursor: pointer;
            outline: none;
            margin-top: 1vw;
            width: 50%;
            padding: .5vw;
            border-radius: 1vw;
            background: linear-gradient(23deg, rgba(55,167,185,1) 0%, rgba(9,64,112,1) 100%);
            color: #fff;
            box-shadow: 6px 5px 4px 0px rgb(0 0 0 / 25%);
            border: none;
        }
    }
}