.precontainer-qlt{
    width: 100%;
    height: 85vh ;
    padding-top: 15vh ;
    position: relative;
    background: rgb(9,64,112);
    background: linear-gradient(152deg, rgba(9,64,112,1) 0%, rgba(56,168,185,1) 100%);
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    overflow-y: auto;
    * {
        font-family: "Poppins";
    }

    .qlt-general{
        width: 100%;
        height: 78vh;
        overflow: auto;
        display: flex;
        flex-direction: column;
        text-align: center;
        .qlt-images{
            display: flex;
            position: relative;
            justify-content: center;
            .message-img{
                width: 18vw;
                  
            }  

            .message-img-child{
                height: 6vh;
                cursor: pointer;
                  
            }
        }


        .Quality_finish-subtitle{  
            font-size: 2.5vw;
            margin-top: 7vh;
            margin-right: 4vw;
            color: #ffffff;
            margin-bottom: 2vw;

        }

        .QualityAtq-number{
            font-size: 2vw;
            margin-right: 4.5vw;
            color: #ffffff;
            margin-bottom: 2vw;
        }

        .Quality-Messa-Button{
            display: flex;
            justify-content: center;

            .peson-button{
                font-size: 1.5vw;
                color: #094070;
                background-color: #ffffff;
                border-radius: 20px;
                cursor: pointer;
                display: flex;
                margin-right: 4.5vw;
                text-transform: uppercase;
                padding: 6px 1rem;
                min-width: 14vw;
                border: none;
                justify-content: center;
                text-transform: uppercase;
                box-shadow: 3px 4px #414040;
                opacity: .7;

            }
        }
    }
}
